import { question_list,get_tags } from '../../api/api.js'
import uniPopup from "../../components/uni-popup/uni-popup.vue"
import uniCollapse from "../../components/uni-collapse/uni-collapse.vue"
import uniCollapseItem from "../../components/uni-collapse-item/uni-collapse-item.vue"
export default {
	components: {
		uniPopup,
		uniCollapse,
		uniCollapseItem
		},
	data() {
		return {
			type: 1,
			searchContent:'',
			tabCurrentIndex:0,
			questionList: [],
			tagList: [],
			tags: '',
			checkedList: [],
			navList: [
				{
					state: 0,
					text: '推荐'
				},
				{
					state: 1,
					text: '最新'
				},
				{
					state: 2,
					text: '热门'
				},
				{
					state: 3,
					text: '冷门'
				}
			],
		}
	},
	onLoad() {
		let that = this
		uni.getStorage({
		    key: 'access_token',
		    success: function (res) {
		        that.access_token = res.data
		    }
		});
		uni.getStorage({
		    key: 'member_id',
		    success: function (res) {
		        that.member_id = res.data
		    }
		});
		console.log(that.member_id)
		console.log(that.access_token)
		that.getHotQuestion()
		that.getTags()
	},
	methods: {
		// tab切换
		tabClick(index){
			let that = this
			this.tabCurrentIndex = index
			if(index == 0){ //1-推荐，2-最新，3-热门，4-冷门
				that.type = 1
			}else if(index == 1){
				that.type = 2
			}else if(index == 2) {
				that.type = 3
			}else if(index == 2){
				that.type = 4
			}
			that.tags = ''
			that.getHotQuestion()
		},
		//点击跳转问题
		toDetail(id){
			uni.navigateTo({
				url: '../questionDetail/questionDetail?id=' + id,
			})
		},
		//获取标签列表
		getTags(){
			let that = this
			get_tags({
				member_id: that.member_id,
				access_token: that.access_token
			}).then(res => {
				console.log(res)
				if(res.data.code == 1){
					that.tagList = res.data.data.list
					console.log(that.tagList)
					// 把所有标签置为未选中
					that.tagList.forEach((item,index) =>{
						item.child.forEach((childItem,index1) =>{
							childItem.is_checked = 0
						})
					})
				}
			})
		},
		// 选中标签
		checkTag(index,tagsIndex){
			let that = this
			// that.checkedList.push(that.tagList[index].child[tagsIndex])
			that.checkedList = []
			that.tagList[index].child[tagsIndex].is_checked = 1
			that.tagList.forEach((item,index1) =>{
				item.child.forEach((items,index2) =>{
					if(items.is_checked == 1){
						that.tags += items.id + ","
						that.checkedList.push(items)
					}
				})
			})
		},
		// 取消选中
		cancelTag(index,tagsIndex){
			let that = this
			that.tagList[index].child[tagsIndex].is_checked = 0
			that.checkedList = []
			that.tagList.forEach((item,index1) =>{
				item.child.forEach((items,index2) =>{
					if(items.is_checked == 1){
						that.tags += items.id + ","
						that.checkedList.push(items)
					}
				})
			})
		},
		//删除选中标签
		deleteTag(item,index){
			let that = this
			that.checkedList.splice(index,1)
			that.tagList.forEach((item1,index) =>{
				item1.child.forEach((items,index2) =>{
					if(items.id == item.id){
						items.is_checked = 0
					}
				})
			})
		},
		// 标签选择确定
		sure_tag(){
			let that = this
			that.checkedList = []
			that.tags = ''
			that.tagList.forEach((item,index1) =>{
				item.child.forEach((items,index2) =>{
					if(items.is_checked == 1){
						that.tags += items.id + ","
					}
				})
			})
			//去除最后一个逗号
			that.tags = that.tags.substr(0, that.tags.length-1);
			that.$refs.popup.close()
			that.getHotQuestion()
		},
		// 重置标签
		reset_tag(){
			let that = this
			that.checkedList = []
			that.tagList.forEach((item,index1) =>{
				item.child.forEach((items,index2) =>{
					items.is_checked = 0
				})
			})
		},
		// 获取热门问答列表
		getHotQuestion(){
			let that = this
			that.questionList = []
			question_list({
				member_id: that.member_id,
				access_token: that.access_token,
				type: that.type, //1-推荐，2-最新，3-热门，4-冷门
				seartag: that.tags,//筛选的标签（多个标签 id 用,号隔开）
				reward : 2,//1-升序，2-降序
				page: 1,
				limit: 10
			}).then(res => {
				console.log(res)
				if(res.data.code == 1){
					that.questionList = res.data.data.list
					// 把所有标签置为未选中
					that.tagList.forEach((item,index) =>{
						item.child.forEach((childItem,index1) =>{
							childItem.is_checked = 0
						})
					})
				}
			})
		},
		// 打开弹出层
		openPopup(){
			this.$refs.popup.open()
		},
		// 关闭弹出层
		closePopup(){
			this.$refs.popup.close()
		}
	}
}
